/**
 * Sistema de An√°lise de Padr√µes para o Quero Paz
 * 
 * Este servi√ßo implementa um sistema simples de aprendizado para identificar
 * padr√µes de chamadas indesejadas e sugerir regras de bloqueio autom√°tico.
 */

import storage from '../storage';

// Constantes para limites e configura√ß√µes
const CALL_HISTORY_LIMIT = 100; // Limite de hist√≥rico para an√°lise
const FREQUENCY_THRESHOLD = 3;  // N√∫mero m√≠nimo de chamadas para identificar um padr√£o
const TIME_WINDOW = 24 * 60 * 60 * 1000; // 24 horas em milissegundos

/**
 * Servi√ßo de an√°lise de padr√µes de chamadas
 */
const patternAnalysis = {
  /**
   * An√°lise b√°sica de frequ√™ncia para identificar n√∫meros repetitivos
   * @returns {Promise<Array>} Lista de n√∫meros com alta frequ√™ncia de chamadas
   */
  async analyzeFrequencyPatterns() {
    // Obt√©m hist√≥rico de chamadas silenciadas
    const silencedCalls = await storage.getSilencedCalls();
    
    // Extrai apenas os n√∫meros de telefone
    const phoneNumbers = silencedCalls.map(call => {
      const parts = call.includes(' - ') ? call.split(' - ') : [call, ''];
      return parts[0].trim();
    });
    
    // Conta a frequ√™ncia de cada n√∫mero
    const frequency = {};
    phoneNumbers.forEach(number => {
      frequency[number] = (frequency[number] || 0) + 1;
    });
    
    // Filtra apenas os n√∫meros que excedem o limite de frequ√™ncia
    const frequentCallers = Object.keys(frequency)
      .filter(number => frequency[number] >= FREQUENCY_THRESHOLD)
      .map(number => ({
        phoneNumber: number,
        frequency: frequency[number],
        confidence: Math.min(frequency[number] / FREQUENCY_THRESHOLD * 100, 100),
        suggestedAction: 'block'
      }));
    
    return frequentCallers;
  },
  
  /**
   * An√°lise de padr√µes temporais (hor√°rios de chamadas)
   * @returns {Promise<Array>} Padr√µes temporais identificados
   */
  async analyzeTimePatterns() {
    const silencedCalls = await storage.getSilencedCalls();
    const timePatterns = [];
    
    // Mapa para armazenar contagens por hora do dia
    const hourlyDistribution = Array(24).fill(0);
    
    // Processa cada chamada para identificar padr√µes de hora
    silencedCalls.forEach(call => {
      // Extrai informa√ß√µes de tempo das chamadas (formato: "... √†s HH:MM")
      const timeMatch = call.match(/√†s (\d{2}):(\d{2})/);
      if (timeMatch) {
        const hour = parseInt(timeMatch[1], 10);
        hourlyDistribution[hour]++;
      }
    });
    
    // Identifica horas com alta frequ√™ncia de chamadas
    for (let hour = 0; hour < 24; hour++) {
      if (hourlyDistribution[hour] >= FREQUENCY_THRESHOLD) {
        timePatterns.push({
          hour,
          frequency: hourlyDistribution[hour],
          confidence: Math.min(hourlyDistribution[hour] / FREQUENCY_THRESHOLD * 100, 100),
          suggestedAction: 'create_time_rule'
        });
      }
    }
    
    return timePatterns;
  },
  
  /**
   * Analisa padr√µes baseados em prefixos comuns (ex: mesmo DDD ou in√≠cio do n√∫mero)
   * @returns {Promise<Array>} Padr√µes de prefixo identificados
   */
  async analyzePrefixPatterns() {
    const silencedCalls = await storage.getSilencedCalls();
    const prefixPatterns = {};
    
    // Extrai n√∫meros e agrupa por prefixos (2 e 3 d√≠gitos)
    silencedCalls.forEach(call => {
      const parts = call.includes(' - ') ? call.split(' - ') : [call, ''];
      const phoneNumber = parts[0].trim().replace(/\D/g, ''); // Remove n√£o-d√≠gitos
      
      if (phoneNumber.length >= 2) {
        // Prefixo de 2 d√≠gitos (geralmente DDD)
        const prefix2 = phoneNumber.substring(0, 2);
        prefixPatterns[prefix2] = (prefixPatterns[prefix2] || 0) + 1;
        
        // Prefixo de 3 d√≠gitos (pode identificar operadoras ou tipos de servi√ßo)
        if (phoneNumber.length >= 3) {
          const prefix3 = phoneNumber.substring(0, 3);
          prefixPatterns[prefix3] = (prefixPatterns[prefix3] || 0) + 1;
        }
      }
    });
    
    // Filtra os prefixos mais comuns
    const significantPrefixes = Object.keys(prefixPatterns)
      .filter(prefix => prefixPatterns[prefix] >= FREQUENCY_THRESHOLD)
      .map(prefix => ({
        prefix,
        length: prefix.length,
        frequency: prefixPatterns[prefix],
        confidence: Math.min(prefixPatterns[prefix] / FREQUENCY_THRESHOLD * 100, 100),
        suggestedAction: 'create_prefix_rule'
      }))
      .sort((a, b) => b.frequency - a.frequency); // Ordena por frequ√™ncia
    
    return significantPrefixes;
  },
  
  /**
   * Gera recomenda√ß√µes de bloqueio com base nas an√°lises
   * @returns {Promise<Array>} Recomenda√ß√µes de bloqueio
   */
  async generateBlockingRecommendations() {
    const [frequencyPatterns, timePatterns, prefixPatterns] = await Promise.all([
      this.analyzeFrequencyPatterns(),
      this.analyzeTimePatterns(),
      this.analyzePrefixPatterns()
    ]);
    
    // Combina todas as recomenda√ß√µes
    const recommendations = [
      // Recomenda√ß√µes espec√≠ficas de n√∫mero
      ...frequencyPatterns.map(pattern => ({
        type: 'number',
        value: pattern.phoneNumber,
        reason: `Ligou ${pattern.frequency} vezes`,
        confidence: pattern.confidence,
        action: 'block_number'
      })),
      
      // Recomenda√ß√µes baseadas em hora
      ...timePatterns.map(pattern => ({
        type: 'time',
        value: `${pattern.hour}:00`,
        reason: `${pattern.frequency} chamadas indesejadas neste hor√°rio`,
        confidence: pattern.confidence,
        action: 'block_time'
      })),
      
      // Recomenda√ß√µes baseadas em prefixo
      ...prefixPatterns.filter(p => p.confidence > 70).map(pattern => ({
        type: 'prefix',
        value: pattern.prefix,
        reason: `${pattern.frequency} chamadas com este prefixo`,
        confidence: pattern.confidence,
        action: 'block_prefix'
      }))
    ];
    
    // Ordena por confian√ßa (mais confi√°vel primeiro)
    return recommendations.sort((a, b) => b.confidence - a.confidence);
  }
};

export default patternAnalysis;
