/**
 * Perfis de Bloqueio Pr√©-configurados para o Quero Paz
 * 
 * Este servi√ßo permite criar e gerenciar modos de bloqueio pr√©-configurados
 * como "Trabalho", "Sono", "F√©rias" com regras espec√≠ficas para cada um.
 */

import storage from '../storage';
import smartRules, { RULE_TYPES, RULE_STATUS } from './smartRules';

// Tipos de perfis predefinidos
export const PROFILE_TYPES = {
  SLEEP: 'sleep',        // Modo sono/noturno
  WORK: 'work',          // Modo trabalho/foco
  VACATION: 'vacation',  // Modo f√©rias/descanso
  MEETING: 'meeting',    // Modo reuni√£o
  CUSTOM: 'custom'       // Perfil personalizado
};

// Estados de perfil
export const PROFILE_STATUS = {
  ACTIVE: 'active',
  INACTIVE: 'inactive',
  SCHEDULED: 'scheduled' // Ativa√ß√£o agendada
};

/**
 * Servi√ßo de perfis de bloqueio
 */
const blockProfiles = {
  /**
   * Obt√©m todos os perfis configurados
   * @returns {Promise<Array>} Lista de perfis
   */
  async getProfiles() {
    const profiles = await storage.getItem('block_profiles');
    return profiles || [];
  },
  
  /**
   * Verifica se existe algum perfil ativo
   * @returns {Promise<Object|null>} Perfil ativo ou null
   */
  async getActiveProfile() {
    const profiles = await this.getProfiles();
    return profiles.find(profile => profile.status === PROFILE_STATUS.ACTIVE) || null;
  },
  
  /**
   * Cria um novo perfil de bloqueio
   * @param {Object} profile - Dados do perfil
   * @returns {Promise<Object>} Perfil criado
   */
  async createProfile(profile) {
    const profiles = await this.getProfiles();
    
    // Gera um ID √∫nico para o perfil
    const newProfile = {
      ...profile,
      id: Date.now().toString(),
      createdAt: new Date().toISOString(),
      status: profile.status || PROFILE_STATUS.INACTIVE,
      ruleIds: profile.ruleIds || []
    };
    
    await storage.setItem('block_profiles', [...profiles, newProfile]);
    return newProfile;
  },
  
  /**
   * Atualiza um perfil existente
   * @param {string} profileId - ID do perfil
   * @param {Object} updates - Dados a atualizar
   * @returns {Promise<boolean>} Sucesso da opera√ß√£o
   */
  async updateProfile(profileId, updates) {
    const profiles = await this.getProfiles();
    const index = profiles.findIndex(p => p.id === profileId);
    
    if (index === -1) return false;
    
    profiles[index] = {
      ...profiles[index],
      ...updates,
      updatedAt: new Date().toISOString()
    };
    
    await storage.setItem('block_profiles', profiles);
    return true;
  },
  
  /**
   * Exclui um perfil
   * @param {string} profileId - ID do perfil
   * @returns {Promise<boolean>} Sucesso da opera√ß√£o
   */
  async deleteProfile(profileId) {
    const profiles = await this.getProfiles();
    const newProfiles = profiles.filter(p => p.id !== profileId);
    
    if (newProfiles.length === profiles.length) return false;
    
    await storage.setItem('block_profiles', newProfiles);
    return true;
  },
  
  /**
   * Ativa um perfil espec√≠fico
   * @param {string} profileId - ID do perfil
   * @returns {Promise<boolean>} Sucesso da opera√ß√£o
   */
  async activateProfile(profileId) {
    const profiles = await this.getProfiles();
    
    // Desativa todos os perfis primeiro
    const updatedProfiles = profiles.map(p => ({
      ...p,
      status: p.id === profileId ? PROFILE_STATUS.ACTIVE : PROFILE_STATUS.INACTIVE
    }));
    
    await storage.setItem('block_profiles', updatedProfiles);
    
    // Ativa as regras associadas a este perfil
    const activeProfile = updatedProfiles.find(p => p.id === profileId);
    if (activeProfile && activeProfile.ruleIds) {
      for (const ruleId of activeProfile.ruleIds) {
        await smartRules.updateRule(ruleId, { status: RULE_STATUS.ACTIVE });
      }
    }
    
    return true;
  },
  
  /**
   * Desativa todos os perfis
   * @returns {Promise<boolean>} Sucesso da opera√ß√£o
   */
  async deactivateAllProfiles() {
    const profiles = await this.getProfiles();
    
    // Marca todos os perfis como inativos
    const updatedProfiles = profiles.map(p => ({
      ...p,
      status: PROFILE_STATUS.INACTIVE
    }));
    
    await storage.setItem('block_profiles', updatedProfiles);
    return true;
  },
  
  /**
   * Cria o perfil "Modo Sono" com configura√ß√µes padr√£o
   * @returns {Promise<Object>} Perfil criado
   */
  async createSleepProfile() {
    // Cria a regra de hor√°rio noturno
    const timeRule = await smartRules.createTimeRule(22, 7, [0, 1, 2, 3, 4, 5, 6], 'Hor√°rio de sono');
    
    // Cria a regra de lista branca para contatos de emerg√™ncia
    const whitelistRule = await smartRules.createWhitelistRule([], 'Contatos de emerg√™ncia');
    
    // Cria o perfil com as regras
    return this.createProfile({
      name: 'Modo Sono',
      type: PROFILE_TYPES.SLEEP,
      description: 'Bloqueia todas as chamadas durante a noite, exceto contatos de emerg√™ncia',
      icon: 'moon',
      color: '#1e40af', // Azul escuro
      ruleIds: [timeRule.id, whitelistRule.id],
      status: PROFILE_STATUS.INACTIVE
    });
  },
  
  /**
   * Cria o perfil "Modo Trabalho" com configura√ß√µes padr√£o
   * @returns {Promise<Object>} Perfil criado
   */
  async createWorkProfile() {
    // Cria regra para hor√°rio comercial (8h-18h, seg-sex)
    const timeRule = await smartRules.createTimeRule(8, 18, [1, 2, 3, 4, 5], 'Hor√°rio de trabalho');
    
    // Cria regra para lista branca (contatos importantes do trabalho)
    const whitelistRule = await smartRules.createWhitelistRule([], 'Contatos de trabalho');
    
    // Cria o perfil
    return this.createProfile({
      name: 'Modo Trabalho',
      type: PROFILE_TYPES.WORK,
      description: 'Filtra chamadas durante o hor√°rio de trabalho, permitindo apenas contatos importantes',
      icon: 'briefcase',
      color: '#15803d', // Verde escuro
      ruleIds: [timeRule.id, whitelistRule.id],
      status: PROFILE_STATUS.INACTIVE
    });
  },
  
  /**
   * Cria o perfil "Modo F√©rias" com configura√ß√µes padr√£o
   * @returns {Promise<Object>} Perfil criado
   */
  async createVacationProfile() {
    // Cria regra para lista branca (apenas fam√≠lia e amigos pr√≥ximos)
    const whitelistRule = await smartRules.createWhitelistRule([], 'Fam√≠lia e amigos pr√≥ximos');
    
    // Cria regra para bloquear chamadas repetitivas (anti-spam)
    const durationRule = await smartRules.createDurationRule(5, 2, 'Anti-spam f√©rias');
    
    // Cria o perfil
    return this.createProfile({
      name: 'Modo F√©rias',
      type: PROFILE_TYPES.VACATION,
      description: 'Bloqueio rigoroso de chamadas, permitindo apenas contatos mais pr√≥ximos',
      icon: 'beach',
      color: '#b91c1c', // Vermelho
      ruleIds: [whitelistRule.id, durationRule.id],
      status: PROFILE_STATUS.INACTIVE
    });
  },
  
  /**
   * Cria o perfil "Modo Reuni√£o" com configura√ß√µes padr√£o
   * @returns {Promise<Object>} Perfil criado
   */
  async createMeetingProfile() {
    // Cria regra para lista branca (apenas contatos cr√≠ticos)
    const whitelistRule = await smartRules.createWhitelistRule([], 'Contatos cr√≠ticos');
    
    // Cria o perfil
    return this.createProfile({
      name: 'Modo Reuni√£o',
      type: PROFILE_TYPES.MEETING,
      description: 'Bloqueio tempor√°rio para reuni√µes importantes',
      icon: 'users',
      color: '#7f1d1d', // Vermelho escuro
      ruleIds: [whitelistRule.id],
      status: PROFILE_STATUS.INACTIVE,
      duration: 60 // Dura√ß√£o padr√£o de 60 minutos
    });
  },
  
  /**
   * Programa a ativa√ß√£o e desativa√ß√£o autom√°tica de um perfil
   * @param {string} profileId - ID do perfil
   * @param {Date} startDate - Data/hora de in√≠cio
   * @param {Date} endDate - Data/hora de fim
   * @returns {Promise<boolean>} Sucesso da opera√ß√£o
   */
  async scheduleProfile(profileId, startDate, endDate) {
    const profiles = await this.getProfiles();
    const index = profiles.findIndex(p => p.id === profileId);
    
    if (index === -1) return false;
    
    // Atualiza o perfil com informa√ß√µes de agendamento
    profiles[index] = {
      ...profiles[index],
      status: PROFILE_STATUS.SCHEDULED,
      schedule: {
        startDate: startDate.toISOString(),
        endDate: endDate.toISOString(),
        active: true
      },
      updatedAt: new Date().toISOString()
    };
    
    await storage.setItem('block_profiles', profiles);
    return true;
  },
  
  /**
   * Verifica e processa perfis agendados
   * Esta fun√ß√£o seria chamada periodicamente ou no carregamento do app
   * @returns {Promise<void>}
   */
  async processScheduledProfiles() {
    const profiles = await this.getProfiles();
    const now = new Date();
    let updated = false;
    
    for (let i = 0; i < profiles.length; i++) {
      const profile = profiles[i];
      
      if (profile.status === PROFILE_STATUS.SCHEDULED && profile.schedule && profile.schedule.active) {
        const startDate = new Date(profile.schedule.startDate);
        const endDate = new Date(profile.schedule.endDate);
        
        // Verifica se √© hora de ativar
        if (now >= startDate && now < endDate && profile.status !== PROFILE_STATUS.ACTIVE) {
          profiles[i] = {
            ...profile,
            status: PROFILE_STATUS.ACTIVE
          };
          updated = true;
          
          // Ativa as regras associadas
          if (profile.ruleIds) {
            for (const ruleId of profile.ruleIds) {
              await smartRules.updateRule(ruleId, { status: RULE_STATUS.ACTIVE });
            }
          }
        }
        // Verifica se √© hora de desativar
        else if (now >= endDate && profile.status !== PROFILE_STATUS.INACTIVE) {
          profiles[i] = {
            ...profile,
            status: PROFILE_STATUS.INACTIVE,
            schedule: {
              ...profile.schedule,
              active: false
            }
          };
          updated = true;
          
          // Desativa as regras, se necess√°rio
          if (profile.ruleIds) {
            for (const ruleId of profile.ruleIds) {
              await smartRules.updateRule(ruleId, { status: RULE_STATUS.INACTIVE });
            }
          }
        }
      }
    }
    
    if (updated) {
      await storage.setItem('block_profiles', profiles);
    }
  }
};

export default blockProfiles;
